import React, { useEffect, useState } from 'react';
import { Bar, Pie, Line } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  BarElement,
  ArcElement,
  Title,
  Tooltip,
  Legend
} from 'chart.js';
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet';
import L from 'leaflet';
import 'leaflet/dist/leaflet.css';
import Navbar from '../components/Navbar';
import Footer from '../components/Footer';
import API from '../services/api'; // <-- CENTRAL API CLIENT
import styles from './Dashboard.module.css';

// Leaflet icon fix
delete L.Icon.Default.prototype._getIconUrl;
L.Icon.Default.mergeOptions({
  iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
  iconUrl: require('leaflet/dist/images/marker-icon.png'),
  shadowUrl: require('leaflet/dist/images/marker-shadow.png')
});

ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement, BarElement, ArcElement, Title, Tooltip, Legend);

const Dashboard = () => {
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    const fetchDashboard = async () => {
      try {
        const res = await API.get('/dashboard'); //  Clean, token auto-added
        setData(res.data);
      } catch (err) {
        setError(err.response?.data?.msg || 'Failed to load dashboard');
      } finally {
        setLoading(false);
      }
    };
    fetchDashboard();
  }, []);

  //  RENDER STATES 

  if (loading) return (
    <div className={styles.appWrapper}>
      <Navbar />
      <div className={styles.pageContent}>
        <div className={styles.loading}>Loading dashboard...</div>
      </div>
      <Footer />
    </div>
  );

  if (error) return (
    <div className={styles.appWrapper}>
      <Navbar />
      <div className={styles.pageContent}>
        <div className={styles.error}>{error}</div>
      </div>
      <Footer />
    </div>
  );

  //  CHARTS 

  const statusChart = {
    labels: ['Open', 'Pending', 'Closed'],
    datasets: [{
      label: 'Cases',
      data: [data.cases.open, data.cases.pending, data.cases.closed],
      backgroundColor: ['#28a745', '#ffc107', '#dc3545']
    }]
  };

  const periodChart = {
    labels: data.crimeRateByPeriod.labels,
    datasets: [{
      label: 'Reported Cases',
      data: data.crimeRateByPeriod.data,
      borderColor: '#007bff',
      backgroundColor: 'rgba(0,123,255,0.1)',
      fill: true
    }]
  };

  const typeChart = {
    labels: data.crimeTypeDistribution.labels,
    datasets: [{
      data: data.crimeTypeDistribution.data,
      backgroundColor: ['#ff6384', '#36a2eb', '#ffce56', '#4bc0c0', '#9966ff', '#ff9f40', '#c9cbcf', '#ff5733', '#c70039', '#900c3f']
    }]
  };

  //  MAIN RENDER 

  return (
    <div className={styles.appWrapper}>
      <Navbar />
      <div className={styles.pageContent}>
        <div className={styles.dashboard}>
          <section className={styles.grid}>
            {/* Case Status */}
            <div className={styles.card}>
              <h3>Case Status</h3>
              <Bar data={statusChart} options={{ responsive: true, plugins: { legend: { display: false }}}} />
            </div>

            {/* Crime Rate */}
            <div className={styles.card}>
              <h3>Crime Rate (Last 12 Months)</h3>
              <Line data={periodChart} options={{ responsive: true }} />
            </div>

            {/* Crime Type */}
            <div className={styles.card}>
              <h3>Crime Type Distribution</h3>
              <Pie data={typeChart} options={{ responsive: true }} />
            </div>

            {/* Heat Map */}
            <div className={styles.cardFull}>
              <h3>Crime Heat Map</h3>
              <MapContainer center={[0.3136, 32.5785]} zoom={6} className={styles.map}>
                <TileLayer
                  url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                  attribution=' OpenStreetMap'
                />
                {data.heatMapData.map((coord, i) => (
                  <Marker key={i} position={[coord[1], coord[0]]}>
                    <Popup>Crime reported here</Popup>
                  </Marker>
                ))}
              </MapContainer>
            </div>
          </section>
        </div>
      </div>
      <Footer />
    </div>
  );
};

export default Dashboard;