import React, { useEffect, useState, useCallback } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import Navbar from '../components/Navbar';
import Footer from '../components/Footer'; // <-- ADD THIS
import styles from './Cases.module.css';
import API from '../services/api';

const Cases = () => {
  const [cases, setCases] = useState([]);
  const [pagination, setPagination] = useState({ current: 1, pages: 1, total: 0, hasNext: false, hasPrev: false });
  const [search, setSearch] = useState('');
  const [debouncedSearch, setDebouncedSearch] = useState('');
  const [expandedId, setExpandedId] = useState(null);
  const [officers, setOfficers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const user = JSON.parse(localStorage.getItem('user') || '{}');

  useEffect(() => {
    const timer = setTimeout(() => {
      setDebouncedSearch(search);
      setPagination(prev => ({ ...prev, current: 1 }));
    }, 500);
    return () => clearTimeout(timer);
  }, [search]);

  const fetchCases = useCallback(async () => {
    setLoading(true);
    try {
      const token = localStorage.getItem('token');
      const params = { search: debouncedSearch, page: pagination.current, limit: 10 };
      const res = await axios.get('http://localhost:5000/api/cases', { params, headers: { Authorization: `Bearer ${token}` } });
      setCases(res.data.cases);
      setPagination(res.data.pagination);
    } catch (err) {
      setError(err.response?.data?.msg || 'Failed to load cases');
    } finally {
      setLoading(false);
    }
  }, [debouncedSearch, pagination.current]);

  useEffect(() => { fetchCases(); }, [fetchCases]);

  useEffect(() => {
    if (user.role === 'admin') {
      const fetchOfficers = async () => {
        const token = localStorage.getItem('token');
        const res = await axios.get('http://localhost:5000/api/cases/officers', { headers: { Authorization: `Bearer ${token}` } });
        setOfficers(res.data);
      };
      fetchOfficers();
    }
  }, [user.role]);

  const updateStatus = async (caseId, newStatus) => {
    try {
      const token = localStorage.getItem('token');
      const res = await axios.patch(`http://localhost:5000/api/cases/${caseId}/status`, { status: newStatus }, { headers: { Authorization: `Bearer ${token}` } });
      setCases(prev => prev.map(c => c._id === caseId ? res.data : c));
    } catch (err) {
      alert(err.response?.data?.msg || 'Update failed');
    }
  };

  const assignOfficer = async (caseId, officerId) => {
    try {
      const token = localStorage.getItem('token');
      const res = await axios.patch(`http://localhost:5000/api/cases/${caseId}/assign`, { officerId }, { headers: { Authorization: `Bearer ${token}` } });
      setCases(prev => prev.map(c => c._id === caseId ? res.data : c));
    } catch (err) {
      alert(err.response?.data?.msg || 'Assignment failed');
    }
  };

  const goToPage = (page) => setPagination(prev => ({ ...prev, current: page }));

  if (loading && cases.length === 0) return (
    <div className={styles.appWrapper}>
      <Navbar />
      <div className={styles.pageContent}>
        <div className={styles.loading}>Loading cases...</div>
      </div>
      <Footer />
    </div>
  );

  if (error) return (
    <div className={styles.appWrapper}>
      <Navbar />
      <div className={styles.pageContent}>
        <div className={styles.error}>{error}</div>
      </div>
      <Footer />
    </div>
  );

  return (
    <div className={styles.appWrapper}>
      <Navbar />
      <div className={styles.pageContent}>
        <div className={styles.container}>
          <div className={styles.searchBar}>
            <input type="text" placeholder="Search by title, type, or reporter..." value={search} onChange={(e) => setSearch(e.target.value)} className={styles.searchInput} />
            {debouncedSearch && <button onClick={() => { setSearch(''); setDebouncedSearch(''); }} className={styles.clearBtn}></button>}
          </div>

          <div className={styles.resultsInfo}>Showing {cases.length} of {pagination.total} cases</div>

          <div className={styles.tableWrapper}>
            <table className={styles.table}>
              <thead><tr><th>Title</th><th>Type</th><th>Status</th><th>Reported By</th><th>Assigned To</th><th>Actions</th></tr></thead>
              <tbody>
                {cases.length === 0 ? (
                  <tr><td colSpan="6" className={styles.noData}>{debouncedSearch ? 'No cases found.' : 'No cases available.'}</td></tr>
                ) : (
                  cases.map(c => (
                    <React.Fragment key={c._id}>
                      <tr className={styles.row} onClick={() => setExpandedId(expandedId === c._id ? null : c._id)}>
                        <td>{c.title}</td>
                        <td>{c.type}</td>
                        <td>
                          <select value={c.status} onChange={(e) => updateStatus(c._id, e.target.value)} onClick={(e) => e.stopPropagation()} className={`${styles.statusSelect} ${styles[c.status]}`}>
                            <option value="open">Open</option>
                            <option value="pending">Pending</option>
                            <option value="closed">Closed</option>
                          </select>
                        </td>
                        <td>{c.reportedBy?.name || 'Citizen'}</td>
                        <td>
                          {user.role === 'admin' ? (
                            <select value={c.assignedTo?._id || ''} onChange={(e) => assignOfficer(c._id, e.target.value)} onClick={(e) => e.stopPropagation()} className={styles.assignSelect}>
                              <option value="">Unassigned</option>
                              {officers.map(o => <option key={o._id} value={o._id}>{o.name}</option>)}
                            </select>
                          ) : c.assignedTo?.name || 'Unassigned'}
                        </td>
                        <td>
                          <button onClick={(e) => { e.stopPropagation(); setExpandedId(expandedId === c._id ? null : c._id); }} className={styles.expandBtn}>
                            {expandedId === c._id ? '' : '+'}
                          </button>
                        </td>
                      </tr>
                      {expandedId === c._id && (
                        <tr>
                          <td colSpan="6" className={styles.expanded}>
                            <div className={styles.detailGrid}>
                              <div><strong>Description:</strong> {c.description || 'N/A'}</div>
                              <div><strong>Location:</strong> {c.location?.coordinates ? `${c.location.coordinates[1].toFixed(4)}, ${c.location.coordinates[0].toFixed(4)}` : 'Not provided'}</div>
                              <div><strong>Reported:</strong> {new Date(c.createdAt).toLocaleString()}</div>
                              <div><strong>Statement:</strong> <Link to={`/statements?case=${c._id}`} className={styles.link}>View / Add</Link></div>
                              <div><strong>Evidence:</strong> <Link to={`/evidence?case=${c._id}`} className={styles.link}>View</Link></div>
                              <div><strong>Timeline:</strong> <Link to={`/timeline/${c._id}`} className={styles.link}>View Full Timeline</Link></div>
                            </div>
                          </td>
                        </tr>
                      )}
                    </React.Fragment>
                  ))
                )}
              </tbody>
            </table>
          </div>

          {pagination.pages > 1 && (
            <div className={styles.pagination}>
              <button onClick={() => goToPage(pagination.current - 1)} disabled={!pagination.hasPrev} className={styles.pageBtn}>Prev</button>
              <span className={styles.pageInfo}>Page {pagination.current} of {pagination.pages}</span>
              <button onClick={() => goToPage(pagination.current + 1)} disabled={!pagination.hasNext} className={styles.pageBtn}>Next</button>
            </div>
          )}
        </div>
      </div>
      <Footer />
    </div>
  );
};

export default Cases;