import React, { useEffect, useState, useCallback } from 'react';
import axios from 'axios';
import { useSearchParams } from 'react-router-dom';
import Navbar from '../components/Navbar';
import Footer from '../components/Footer';   // <-- ADD THIS
import styles from './Statements.module.css';
import API from '../services/api';

const Statements = () => {
  const [searchParams] = useSearchParams();
  const caseId = searchParams.get('case');
  const [statements, setStatements] = useState([]);
  const [pagination, setPagination] = useState({ current: 1, pages: 1, total: 0, hasNext: false, hasPrev: false });
  const [search, setSearch] = useState('');
  const [debouncedSearch, setDebouncedSearch] = useState('');
  const [newContent, setNewContent] = useState('');
  const [editingId, setEditingId] = useState(null);
  const [editContent, setEditContent] = useState('');
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [submitting, setSubmitting] = useState(false);
  const [savingEdit, setSavingEdit] = useState(false);
  const user = JSON.parse(localStorage.getItem('user') || '{}');

  // Debounce search
  useEffect(() => {
    const timer = setTimeout(() => {
      setDebouncedSearch(search);
      setPagination(prev => ({ ...prev, current: 1 }));
    }, 500);
    return () => clearTimeout(timer);
  }, [search]);

  // Fetch statements
  const fetchStatements = useCallback(async () => {
    if (!caseId) return;
    setLoading(true);
    try {
      const token = localStorage.getItem('token');
      const params = { case: caseId, search: debouncedSearch, page: pagination.current, limit: 10 };
      const res = await axios.get('http://localhost:5000/api/statements', { params, headers: { Authorization: `Bearer ${token}` } });
      setStatements(res.data.statements);
      setPagination(res.data.pagination);
    } catch (err) {
      setError(err.response?.data?.msg || 'Failed to load statements');
    } finally {
      setLoading(false);
    }
  }, [caseId, debouncedSearch, pagination.current]);

  useEffect(() => { fetchStatements(); }, [fetchStatements]);

  // Submit new statement
  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!newContent.trim()) return;
    setSubmitting(true);
    try {
      const token = localStorage.getItem('token');
      const res = await axios.post('http://localhost:5000/api/statements', { caseId, content: newContent }, { headers: { Authorization: `Bearer ${token}` } });
      setStatements(prev => [res.data, ...prev]);
      setNewContent('');
      setPagination(prev => ({ ...prev, total: prev.total + 1 }));
    } catch (err) {
      alert(err.response?.data?.msg || 'Failed to save');
    } finally {
      setSubmitting(false);
    }
  };

  // Edit handlers
  const startEdit = (stmt) => { setEditingId(stmt._id); setEditContent(stmt.content); };
  const cancelEdit = () => { setEditingId(null); setEditContent(''); };
  const saveEdit = async (stmtId) => {
    if (!editContent.trim()) return;
    setSavingEdit(true);
    try {
      const token = localStorage.getItem('token');
      const res = await axios.patch(`http://localhost:5000/api/statements/${stmtId}`, { content: editContent }, { headers: { Authorization: `Bearer ${token}` } });
      setStatements(prev => prev.map(s => s._id === stmtId ? res.data : s));
      cancelEdit();
    } catch (err) {
      alert(err.response?.data?.msg || 'Failed to update');
    } finally {
      setSavingEdit(false);
    }
  };

  const goToPage = (page) => setPagination(prev => ({ ...prev, current: page }));
  const canEdit = (stmt) => user.role === 'admin' || stmt.author?._id === user.id;

  //  RENDER 

  // No case selected
  if (!caseId) return (
    <div className={styles.appWrapper}>
      <Navbar />
      <div className={styles.pageContent}>
        <div className={styles.error}>No case selected. Go to <a href="/cases">Cases</a> first.</div>
      </div>
      <Footer />
    </div>
  );

  // Loading
  if (loading && statements.length === 0) return (
    <div className={styles.appWrapper}>
      <Navbar />
      <div className={styles.pageContent}>
        <div className={styles.loading}>Loading statements...</div>
      </div>
      <Footer />
    </div>
  );

  // Error
  if (error) return (
    <div className={styles.appWrapper}>
      <Navbar />
      <div className={styles.pageContent}>
        <div className={styles.error}>{error}</div>
      </div>
      <Footer />
    </div>
  );

  // Main UI
  return (
    <div className={styles.appWrapper}>
      <Navbar />
      <div className={styles.pageContent}>
        <div className={styles.container}>
          {/* Add Statement Form */}
          <div className={styles.formCard}>
            <h3>Add New Statement</h3>
            <form onSubmit={handleSubmit}>
              <textarea
                value={newContent}
                onChange={(e) => setNewContent(e.target.value)}
                placeholder="Type your statement..."
                required
                className={styles.textarea}
                rows="4"
              />
              <button type="submit" disabled={submitting} className={styles.submitBtn}>
                {submitting ? 'Saving...' : 'Save Statement'}
              </button>
            </form>
          </div>

          {/* Search */}
          <div className={styles.searchBar}>
            <input
              type="text"
              placeholder="Search statements..."
              value={search}
              onChange={(e) => setSearch(e.target.value)}
              className={styles.searchInput}
            />
            {debouncedSearch && (
              <button onClick={() => { setSearch(''); setDebouncedSearch(''); }} className={styles.clearBtn}>
                
              </button>
            )}
          </div>

          {/* Results Info */}
          <div className={styles.resultsInfo}>
            Showing {statements.length} of {pagination.total} statements
          </div>

          {/* Statements List */}
          <div className={styles.list}>
            {statements.length === 0 ? (
              <p className={styles.noData}>
                {debouncedSearch ? 'No statements found.' : 'No statements yet. Add one above.'}
              </p>
            ) : (
              statements.map(s => (
                <div key={s._id} className={styles.statementCard}>
                  <div className={styles.meta}>
                    <strong>{s.author?.name || 'Unknown'}</strong>
                    <span className={styles.date}>
                      {new Date(s.createdAt).toLocaleString()}
                      {s.updatedAt && s.updatedAt !== s.createdAt && (
                        <em> (edited {new Date(s.updatedAt).toLocaleString()})</em>
                      )}
                    </span>
                  </div>

                  {editingId === s._id ? (
                    <div className={styles.editMode}>
                      <textarea
                        value={editContent}
                        onChange={(e) => setEditContent(e.target.value)}
                        className={styles.editTextarea}
                        rows="4"
                      />
                      <div className={styles.editActions}>
                        <button
                          onClick={() => saveEdit(s._id)}
                          disabled={savingEdit || !editContent.trim()}
                          className={styles.saveBtn}
                        >
                          {savingEdit ? 'Saving...' : 'Save'}
                        </button>
                        <button onClick={cancelEdit} className={styles.cancelBtn}>
                          Cancel
                        </button>
                      </div>
                    </div>
                  ) : (
                    <>
                      <p className={styles.content}>{s.content}</p>
                      {canEdit(s) && (
                        <button onClick={() => startEdit(s)} className={styles.editBtn}>
                          Edit
                        </button>
                      )}
                    </>
                  )}
                </div>
              ))
            )}
          </div>

          {/* Pagination */}
          {pagination.pages > 1 && (
            <div className={styles.pagination}>
              <button
                onClick={() => goToPage(pagination.current - 1)}
                disabled={!pagination.hasPrev}
                className={styles.pageBtn}
              >
                 Prev
              </button>
              <span className={styles.pageInfo}>
                Page {pagination.current} of {pagination.pages}
              </span>
              <button
                onClick={() => goToPage(pagination.current + 1)}
                disabled={!pagination.hasNext}
                className={styles.pageBtn}
              >
                Next 
              </button>
            </div>
          )}
        </div>
      </div>
      <Footer />
    </div>
  );
};

export default Statements;