import React, { useEffect, useState, useCallback, useRef } from 'react';
import axios from 'axios';
import { useSearchParams } from 'react-router-dom';
import Navbar from '../components/Navbar';
import Footer from '../components/Footer';   // <-- ADD THIS
import styles from './Evidence.module.css';
import API from '../services/api';

const Evidence = () => {
  const [searchParams] = useSearchParams();
  const caseId = searchParams.get('case');
  const statementId = searchParams.get('statement');
  const [evidence, setEvidence] = useState([]);
  const [pagination, setPagination] = useState({ current: 1, pages: 1, total: 0, hasNext: false, hasPrev: false });
  const [search, setSearch] = useState('');
  const [debouncedSearch, setDebouncedSearch] = useState('');
  const [uploading, setUploading] = useState(false);
  const [description, setDescription] = useState('');
  const fileInputRef = useRef(null);
  const user = JSON.parse(localStorage.getItem('user') || '{}');

  // Debounce search
  useEffect(() => {
    const timer = setTimeout(() => {
      setDebouncedSearch(search);
      setPagination(prev => ({ ...prev, current: 1 }));
    }, 500);
    return () => clearTimeout(timer);
  }, [search]);

  // Fetch evidence
  const fetchEvidence = useCallback(async () => {
    if (!caseId) return;
    try {
      const token = localStorage.getItem('token');
      const params = { case: caseId, statement: statementId || undefined, search: debouncedSearch, page: pagination.current, limit: 12 };
      const res = await axios.get('http://localhost:5000/api/evidence', { params, headers: { Authorization: `Bearer ${token}` } });
      setEvidence(res.data.evidence);
      setPagination(res.data.pagination);
    } catch (err) {
      alert(err.response?.data?.msg || 'Failed to load evidence');
    }
  }, [caseId, statementId, debouncedSearch, pagination.current]);

  useEffect(() => { fetchEvidence(); }, [fetchEvidence]);

  // Upload handler
  const handleUpload = async () => {
    const file = fileInputRef.current?.files[0];
    if (!file) return;
    setUploading(true);
    const formData = new FormData();
    formData.append('file', file);
    formData.append('caseId', caseId);
    if (statementId) formData.append('statementId', statementId);
    if (description) formData.append('description', description);
    try {
      const token = localStorage.getItem('token');
      const res = await axios.post('http://localhost:5000/api/evidence/upload', formData, {
        headers: { Authorization: `Bearer ${token}`, 'Content-Type': 'multipart/form-data' }
      });
      setEvidence(prev => [res.data, ...prev]);
      setPagination(prev => ({ ...prev, total: prev.total + 1 }));
      fileInputRef.current.value = '';
      setDescription('');
    } catch (err) {
      alert(err.response?.data?.msg || 'Upload failed');
    } finally {
      setUploading(false);
    }
  };

  // Delete evidence
  const deleteEvidence = async (id) => {
    if (!window.confirm('Delete this evidence?')) return;
    try {
      const token = localStorage.getItem('token');
      await axios.delete(`http://localhost:5000/api/evidence/${id}`, { headers: { Authorization: `Bearer ${token}` } });
      setEvidence(prev => prev.filter(e => e._id !== id));
      setPagination(prev => ({ ...prev, total: prev.total - 1 }));
    } catch (err) {
      alert('Delete failed');
    }
  };

  const getFileUrl = (filename) => `http://localhost:5000/api/evidence/file/${filename}`;

  //  RENDER 

  // No case selected
  if (!caseId) return (
    <div className={styles.appWrapper}>
      <Navbar />
      <div className={styles.pageContent}>
        <div className={styles.error}>Select a case first.</div>
      </div>
      <Footer />
    </div>
  );

  // Main UI
  return (
    <div className={styles.appWrapper}>
      <Navbar />
      <div className={styles.pageContent}>
        <div className={styles.container}>
          {/* Upload Card */}
          <div className={styles.uploadCard}>
            <h3>Upload Evidence</h3>
            <input type="file" ref={fileInputRef} accept="image/*,video/*,audio/*" className={styles.fileInput} />
            <input
              type="text"
              placeholder="Description (optional)"
              value={description}
              onChange={(e) => setDescription(e.target.value)}
              className={styles.descInput}
            />
            <button
              onClick={handleUpload}
              disabled={uploading || !fileInputRef.current?.files[0]}
              className={styles.uploadBtn}
            >
              {uploading ? 'Uploading...' : 'Upload'}
            </button>
          </div>

          {/* Search Bar */}
          <div className={styles.searchBar}>
            <input
              type="text"
              placeholder="Search by name or description..."
              value={search}
              onChange={(e) => setSearch(e.target.value)}
              className={styles.searchInput}
            />
            {debouncedSearch && (
              <button onClick={() => { setSearch(''); setDebouncedSearch(''); }} className={styles.clearBtn}>
                
              </button>
            )}
          </div>

          {/* Results Info */}
          <div className={styles.resultsInfo}>
            Showing {evidence.length} of {pagination.total} items
          </div>

          {/* Evidence Grid */}
          <div className={styles.grid}>
            {evidence.map(e => (
              <div key={e._id} className={styles.mediaCard}>
                {e.mimetype.startsWith('image/') ? (
                  <img src={getFileUrl(e.filename)} alt={e.originalName} className={styles.media} />
                ) : e.mimetype.startsWith('video/') ? (
                  <video controls className={styles.media}>
                    <source src={getFileUrl(e.filename)} type={e.mimetype} />
                  </video>
                ) : e.mimetype.startsWith('audio/') ? (
                  <audio controls className={styles.media}>
                    <source src={getFileUrl(e.filename)} type={e.mimetype} />
                  </audio>
                ) : (
                  <div className={styles.unknown}>File</div>
                )}
                <div className={styles.info}>
                  <strong>{e.originalName}</strong>
                  <small>
                    {(e.size / 1024).toFixed(1)} KB  {new Date(e.createdAt).toLocaleDateString()}
                  </small>
                  {e.description && <p className={styles.desc}>{e.description}</p>}
                  <small>By: {e.uploadedBy?.name}</small>
                </div>
                {(user.role === 'admin' || e.uploadedBy?._id === user.id) && (
                  <button onClick={() => deleteEvidence(e._id)} className={styles.deleteBtn}>
                    Delete
                  </button>
                )}
              </div>
            ))}
          </div>

          {/* Pagination */}
          {pagination.pages > 1 && (
            <div className={styles.pagination}>
              <button
                onClick={() => setPagination(p => ({ ...p, current: p.current - 1 }))}
                disabled={!pagination.hasPrev}
                className={styles.pageBtn}
              >
                 Prev
              </button>
              <span>Page {pagination.current} of {pagination.pages}</span>
              <button
                onClick={() => setPagination(p => ({ ...p, current: p.current + 1 }))}
                disabled={!pagination.hasNext}
                className={styles.pageBtn}
              >
                Next 
              </button>
            </div>
          )}
        </div>
      </div>
      <Footer />
    </div>
  );
};

export default Evidence;